import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("albums");
export default {
  computed: {
    ...mapState(["userAlbums", "selectedAlbum"]),
  },
  methods: {
    ...mapActions({
      $_fetchUserAlbums: "fetchUserAlbums",
      $_fetchAlbumById: "fetchAlbumById",
    }),
  },
};
